html {
  height: 5000px; /* La altura es igual al 100% de la altura de la ventana */
  width: 100%; /* Ocupa todo el ancho */
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
  background: linear-gradient(30deg, #c9c9c9, #2b2b2b, #1a1a1a);
  color: rgb(250, 252, 252);
}

h1 {
  text-align: center;
  color: rgb(247, 243, 246);
  margin: 15px;
}
@media (min-width: 768px) {
  h1 {
    text-align: center;
    color: rgb(247, 243, 246);
    margin: 15px;
  }
}

.pPending {
  font-size: 20px;
  padding-right: auto;
  font-style: italic;
}
hr {
  margin-top: 10px;
  padding: 0;
}

p {
  font-size: 25px;
  word-wrap: break-word;
  margin-top: 20px;
  margin-bottom: 0;
}
.pComment {
  font-size: 18px;
  color: rgba(74, 238, 82, 0.911);
  margin-top: 0;
  padding-top: 10px;
}
.container-coment {
  padding: 0;
  margin-top: 0;
}

.pTime {
  font-size: 15px;
  color: rgb(216, 191, 240);
}

.container {
  height: auto; /* Límite de altura */
  /*overflow-y: auto;  Habilita el scroll vertical */
  margin-top: 100px;
  margin-bottom: 30px;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0, 0, 0, 0.562);
  box-shadow: 0 4px 8px rgba(184, 44, 184, 0.692);
  padding: 30px;
  border-radius: 20px;
}

/* Media query para pantallas de 768px o menos */
@media (max-width: 1680px) {
  .container {
    height: auto; /* Límite de altura */
    /*overflow-y: auto;  Habilita el scroll vertical */
    width: 40%; /* Ajusta el ancho al 90% de la pantalla */
    padding: 20px; /* Reduce un poco el padding para mejor ajuste en pantallas pequeñas */
    margin-top: 100px; /* Reduce el margen superior */
  }
}

@media (max-width: 768px) {
  .container {
    height: auto; /* Límite de altura */
    /*overflow-y: auto;  Habilita el scroll vertical */
    width: 85%; /* Ajusta el ancho al 90% de la pantalla */
    padding: 20px; /* Reduce un poco el padding para mejor ajuste en pantallas pequeñas */
    margin-top: 100px; /* Reduce el margen superior */
  }
}

.textarea-comen {
  display: flex;
  width: 95%;
  font-style: italic;
  align-items: stretch;
}

.input-search {
  display: flex;
  width: 95%;
  font-style: italic;
  align-items: stretch;
}

@media (max-width: 500px) {
  .input-search {
    display: flex;
    width: auto;
    font-style: italic;
    align-items: stretch;
  }
}
@media (max-width: 500px) {
  .textarea-comen {
    display: flex;
    justify-content: center;
    width: auto;
    font-style: italic;
    align-items: stretch;
  }
}

.flex {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}
.flex-edit {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}

@media (min-width: 768px) {
  .flex-edit {
    display: flex;
    align-items: center;
    flex-direction: row;
  }
}

@media (min-width: 768px) {
  .flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: first baseline;
}

input,
textarea {
  display: flex;
  padding: 15px 2px;
  outline: none;
  width: 90%;
  margin-top: 10px;
  font-size: 15px;
  padding: 8px 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  outline: none;
  transition: box-shadow 0.3s ease-in-out;
}

input,
textarea:focus {
  box-shadow: 0 0 8px 4px rgba(185, 95, 230, 0.3); /* Ajusta el color y el tamaño del resplandor */
  border-color: rgba(184, 186, 189, 0.6);
}

.li-container {
  max-width: auto;
  color: rgb(253, 253, 253);
  list-style: none;
  background: rgba(85, 84, 84, 0.562);
  border-radius: 10px;
  margin-top: 1rem;
  padding: 10px;
}

.task-completed {
  background-color: #1f3825; /* Fondo verde claro */
  position: relative; /* Necesario para el icono flotante */
}

.check-icon {
  content: "✔";
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
  color: #28a745;
}

.confirmation-message {
  position: absolute;
  top: 10px;
  left: 27%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 40px;
  margin-left: auto;
  margin-top: 20px;
  margin-right: 20px;
  margin-bottom: 0;
  background-color: rgba(255, 255, 255, 0.911);
  box-shadow: 0 0 8px 4px rgba(185, 95, 230, 0.3);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  color: #02c442;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
}

.confirmation-message-delete {
  position: absolute;
  top: 10px;
  left: 27%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 40px;
  margin-left: auto;
  margin-top: 20px;
  margin-right: 20px;
  margin-bottom: 0;
  background-color: rgba(255, 255, 255, 0.911);
  box-shadow: 0 0 8px 4px rgba(185, 95, 230, 0.3);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  color: #ee0606;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
}

