.task-done {
  color: rgb(167, 163, 163);
  text-decoration: line-through;
  text-decoration-color: rgb(209, 208, 208);
  /* Cambia 'red' al color que prefieras */
}
.p-coment {
  padding: 0;
  margin-top: 0;
  font-size: 17px;
}
.coment-done {
  color: rgb(167, 163, 163);
  text-decoration: line-through;
  text-decoration-color: rgb(209, 208, 208);
  padding: 0;
  margin-top: 0;
  font-size: 17px;
}

.btn-delete {
